/* Safari, Chrome and Opera > 12.1 */

/* Internet Explorer */

/* Opera < 12.1 */
.intro {
  padding-left: 5rem;
  display: flex;
  cursor: default;
}

.intro .container {
  display: flex;
  letter-spacing: 0.1rem;
}

.intro-greeting {
  font-weight: 700;
  font-size: 5rem;
  padding-bottom: 1rem;
}

.intro-greeting div {
  height: 5rem !important;
}

.intro-introduce {
  font-weight: 700;
  font-size: 2rem;
  padding-bottom: 1.25rem;
}

.intro-introduce div {
  height: 2rem !important;
}

.intro-role {
  letter-spacing: 0rem !important;
  opacity: 0.9;
  padding-bottom: 1.5rem;
}

.intro-role div {
  height: 1rem !important;
}

.icon {
  fill: #fff;
  opacity: 0.8;
  width: 20px;
  height: 20px;
}

.icon:hover {
  fill: #2be2ff;
  opacity: 1;
}

.intro-socials {
  display: flex;
}

.intro-socials div {
  width: 20px !important;
  height: 20px !important;
  padding-right: 1rem;
}

@media screen and (max-width: 760px) {
  .intro-greeting {
    font-size: 4rem;
    padding-bottom: 0rem;
  }

  .intro-greeting div {
    height: 4rem;
  }

  .intro-introduce {
    font-size: 1.5rem;
    letter-spacing: 0.05rem !important;
    padding-bottom: 0.75rem;
  }

  .intro-introduce div {
    height: 1.5rem;
  }

  .intro-role {
    font-size: 0.9rem;
    padding-bottom: 1rem;
  }

  .intro-role div {
    height: 0.9rem;
  }

  .icon {
    width: 16px;
    height: 16px;
  }

  .intro-socials div {
    width: 16px !important;
    height: 16px !important;
    padding-right: 0.75rem;
  }
}

@media screen and (max-width: 640px) {
  .intro-greeting {
    font-size: 3rem;
    padding-bottom: 0.25rem;
  }

  .intro-greeting div {
    height: 3rem !important;
  }

  .intro-introduce {
    font-size: 1.25rem;
    letter-spacing: 0.05rem !important;
    padding-bottom: 0.5rem;
  }

  .intro-introduce div {
    height: 1.25rem !important;
  }

  .intro-role {
    font-size: 0.8rem;
  }

  .intro-role div {
    height: 0.8rem !important;
  }

  .icon {
    width: 15px;
    height: 15px;
  }

  .intro-socials div {
    width: 15px !important;
    height: 15px !important;
    padding-right: 0.5rem;
  }
}

@media screen and (max-width: 480px) {
  .intro {
    padding-left: 2rem;
  }
}

@media screen and (max-width: 400px) {
  .intro {
    padding-left: 1rem;
  }

  .intro-greeting {
    font-size: 2.75rem;
  }

  .intro-greeting div {
    height: 2.75rem !important;
  }

  .intro-introduce {
    font-size: 1rem;
  }
  
  .intro-role {
    font-size: 0.75rem;
    /* padding: 0.25rem 0 0.5rem; */
  }

  .intro-role div {
    height: 0.75rem !important;
  }

  .icon {
    width: 15px;
    height: 15px;
  }

  .intro-socials div {
    width: 15px !important;
    height: 15px !important;
    padding-right: 0.5rem;
  }
}


.canvas-sphere canvas:focus {
  outline: none;
}

.canvas-sphere {
  position: absolute !important;
}
.home {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
}

.home__spheres {
  position: fixed !important;
  width: 100% !important;
  height: 100%;
  z-index: -1; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 5s;
}

